html,
body {
    height: 100%;
}

.navbar {
    background-color: #fff !important;
}

.f-sm {
    width: 16px;
    height: 16px;
    vertical-align: text-bottom;
}

#relatedSearch a, #collapseAdvanced a {
    text-decoration: none;
}

#searchButton {
    width: 250px;
}

#ddUserMenu {
    min-width: 270px;
    position: absolute !important;
}

#dropdownUserMenu {
    text-decoration: none;
}

#dropdownHistoryUsers {
    width: 250px;
}

#collapseAdvanced .card {
    border: 0;
}

#profileImage {
    width: 150px;
}

.form-control::-webkit-input-placeholder {
    color: #bbb;
}

.text-sm {
    font-size: .90rem;
    line-height: 1rem;
}

.popover-header {
    background-color: #fff !important;
}

.popover-x {
    border: 0;
}

.bootstrap-select {
    background-color: #fff !important;
}

.img {
    width: 50px;
    height: 50px;
}
.error-text {
    color: #fff;
    padding: 10px 20px;
    background-color: #ff5f6e;
    border-radius: 6px;
}
.error-input {
    border: 1px solid #e45d5d;
    box-shadow: 0 0 3px #e45d5d;
}
.profile-pic {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: 0 auto;
    transform: translate(-50%, -50%);
}
#profileImage .carousel-item {
    border-radius: 50%;
    height: 190px;
    overflow: hidden;
}

.list-inline-item {
    cursor: pointer;
}

